import 'react-app-polyfill/ie11';


import React, { useState } from 'react';
import ReactDOM from 'react-dom';

import { Product } from 'components/ui';

import catImageSrc from 'static/images/cat.png';

import './styles/_common.scss';
import './_index.scss';

const Root = () => {
    const [productsStatuses, setProductsStatuses] = useState({ 3: 'disabled' });

    const products = [
        {
            id: 1,
            title: 'Нямушка',
            subtitle: 'с фуа-гра',
            text: 'Сказочное заморское яство',
            description: 'Печень утки разварная с артишоками.',
            servingCount: 10,
            giftCount: 1,
            weight: 1,
            imageSrc: catImageSrc
        },

        {
            id: 2,
            title: 'Нямушка',
            subtitle: 'с рыбой',
            text: 'Сказочное заморское яство',
            description: 'Головы щучьи с чесноком да свежайшая сёмгушка.',
            servingCount: 40,
            giftCount: 2,
            weight: 2,
            imageSrc: catImageSrc
        },

        {
            id: 3,
            title: 'Нямушка',
            subtitle: 'с курой',
            text: 'Сказочное заморское яство',
            description: 'Головы щучьи с чесноком да свежайшая сёмгушка.',
            servingCount: 100,
            giftCount: 5,
            weight: 5,
            imageSrc: catImageSrc
        }
    ];

    function handleProductDispatchAction(id, type) {
        const status = productsStatuses[id];

        if (status !== 'disabled') {
            switch (type) {
                case 'select': {
                    const newStatus = status === 'selected' ? 'default' : 'selected';
    
                    setProductsStatuses(prev => ({ ...prev, [id]: newStatus }));
    
                    break;
                }
    
                default:
                    break;
            }
        }
    }
    
    return (
        <div className = 'Root__content'>
            <h1 className = 'Root__title'>
                Ты сегодня покормил кота?
            </h1>

            <ul className = 'Root__products'>
                { products.map(product => (
                    <li className = 'Root__productsItem' key = { product.id }>
                        <Product
                            product = { product }
                            status = { productsStatuses[product.id] }
                            dispatchAction = { handleProductDispatchAction }
                        />
                    </li>
                )) }
            </ul>
        </div>
    )
};

ReactDOM.render(<Root />, document.querySelector('.Root'));

