function classNames() {
    const classes = [];
    
    for (let i = 0; i < arguments.length; i++) {
        if (!arguments[i]) continue;

        const argument = arguments[i];
        const type = Object.prototype.toString.call(argument);

        switch (type) {
            case '[object Array]':
                classes.push(classNames(...argument));
                break;
            case '[object Object]':
                for (let key in argument) {
                    if (argument.hasOwnProperty(key) && argument[key]) {
                        classes.push(key);
                    }
                }

                break;
            default:
                classes.push(argument);
                break;
        }
    }

    return classes.join(' ');
}

export { classNames };