import React from 'react';

import { ReactComponent as ProductBackground } from 'static/icons/product-background.svg';

import { classNames } from 'helpers';

const Icon = ({ name, className, ...restProps }) => {
    const IconClass = classNames('Icon', className);

    switch(name) {
        case 'product-background':
            return <ProductBackground className = { IconClass } { ...restProps } />
        default:
            return null
    }
};

export { Icon };
