import React, { useState } from 'react';

import { Icon } from 'components/ui';

import IS_MOBILE from 'constants/IS_MOBILE';
import { classNames, getDeclination } from 'helpers';
import { closest } from 'polyfills';

import './_Product.scss';

const Product = ({ product, status = 'default', dispatchAction }) => {
    const [isHovered, setIsHovered] = useState(false);

    const { id, title, subtitle, servingCount } = product;
    const { giftCount, weight, imageSrc } = product;
    
    const servingDeclinations = ['порция', 'порции', 'порций'];
    const servingDeclination = getDeclination(servingCount, servingDeclinations);

    const giftCountDeclinations = ['мышь', 'мыши', 'мышей'];
    const giftCounteclination = getDeclination(giftCount, giftCountDeclinations);
    
    const description = {
        default: 'Чего сидишь? Порадуй котэ,',
        selected: product.description,
        disabled: `Печалька, ${product.subtitle.toLowerCase()} закончился.`
    };

    const isHoveredText =  status === 'selected' && (isHovered || IS_MOBILE);
    const text = isHoveredText ? 'Котэ не одобряет?' : product.text;

    const productClass = classNames('Product', {
        'Product--selected': status === 'selected',
        'Product--disabled': status === 'disabled'
    });

    function getDescriptionClass({ color, isAction } = {}) {
        return classNames('Product__description', {
            'Product__description--color-blue': color === 'blue',
            'Product__description--action': isAction
        });
    }

    function getTextClass({ size, weight, color } = {}) {
        return classNames('Product__text', {
            'Product__text--size-sm': size === 'sm',
            'Product__text--weight-bold': weight === 'bold',
            'Product__text--color-pink': color === 'pink'
        });
    }

    function handleProductClick(event) {
        handleProductActionClick(event);
    }

    function handleProductActionClick({ target }) {
        const action = closest(target, '[data-action-type]');

        if (action && dispatchAction) {
            dispatchAction(id, action.dataset.actionType);
        }
    }

    function handleContentMouseEnter() {
        if (status === 'selected' && !IS_MOBILE) {
            setIsHovered(true);
        }
    }

    function handleContentMouseLeave() {
        if (isHovered && !IS_MOBILE) {
            setIsHovered(false);
        }
    }
    
    return (
        <div className = { productClass } onClick = { handleProductClick }>
            <div
                className = 'Product__content'
                data-action-type = 'select'
                onMouseEnter = { handleContentMouseEnter }
                onMouseLeave = { handleContentMouseLeave }
            >
                <div className = 'Product__info'>
                    <span className = { getTextClass({ color: isHoveredText ? 'pink' : '' }) }>
                        { text }
                    </span>

                    <h3 className = 'Product__title'>
                        { title }
                    </h3>

                    <h4 className = 'Product__subtitle'>
                        { subtitle }
                    </h4>

                    <div className = 'Product__details'>
                        <span className = { getTextClass({ size: 'sm' }) }>
                            <span className = { getTextClass({ weight: 'bold' }) }>
                                { servingCount }
                            </span>
                            &nbsp;
                            { servingDeclination }
                        </span>

                        <span className = { getTextClass({ size: 'sm' }) }>
                            <span className = { getTextClass({ weight: 'bold' }) }>
                                { giftCount }
                            </span>
                            &nbsp;
                            { giftCounteclination }
                            &nbsp;
                            { 'в подарок' }
                        </span>
                    </div>
                </div>

                <div className = 'Product__imageWrap'>
                    <img
                        className = 'Product__image'
                        src = { imageSrc }
                        alt = 'Фотография товара'
                    />
                </div>

                <div className = 'Product__weight'>
                    <span className = 'Product__weightCount'>
                        { weight }
                    </span>

                    <span className = 'Product__weightUnit'>
                        кг
                    </span>
                </div>

                <Icon
                    name = 'product-background'
                    className = 'Product__background'
                />
            </div>

            <span className = { getDescriptionClass() }>
                { status === 'default' ? (
                    <>
                        { description[status] }
                        &nbsp;
                        <span className = { getDescriptionClass({ color: 'blue' }) }>
                            <span
                                className = { getDescriptionClass({ color: 'blue', isAction: true }) }
                                data-action-type = 'select'
                            >
                                купи
                            </span>
                            .
                        </span>
                    </>
                ) : (
                    <>
                        { description[status] }
                    </>
                ) }
            </span>
        </div>
    )
};

export { Product };